<template>
    <section>
  <div class="titol">
        <h1>UNEIX-TE A UART</h1>
    </div>
  <div class="container">
    <section class="text" >
        <h3><b>ETS ARTISTA I VOLDRIES PUBLICAR LES TEVES OBRES A U ART ?</b></h3>
        <p>Omple el formulari i ens posarem en contacte amb tu.</p>
    </section>
        
  <!-- Formulario para añadir personas -->
  
  <section class="form">
    <form action="" class="text-center">
        <input v-model="nombre" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Nom" style="margin-bottom: 2%;">
        <input v-model="edad" @keyup.enter="crearPersona" type="text" name="Email" placeholder="Email" class="form-control">
        
        <!-- Botón para añadir con color violeta oscuro -->
        <input @click="crearPersona" type="button" value="Añadir" class="btn btn-custom-violeta" style="margin-top: 2rem">
    </form>
</section>


          
  <!-- Tabla donde se muestran los datos -->
          <section class="data">
              <p style="font-weight: bold;">Aquest és el teu ID :</p>
              <table class="table">
                  <thead>
                      <tr>
                          <th scope="col">id</th>
                          
                      </tr>
                  </thead>
                  <tbody>
                      <tr v-for="(persona, index) in personas" :key="persona.id">
                          <td>{{ persona.id }}</td>
                          <td>
                              <span v-if="formActualizar && idActualizar == index">
                                  
                                  <!-- Formulario para actualizar -->
                                  <input v-model="nombreActualizar" type="text" class="form-control">
                              </span>
                           
                              
                              <!-- Botón para guardar la información actualizada -->
                             
                          </td>
                      </tr>
                  </tbody>
              </table>
              <div class="motiv">
                <h3><b> ELLS JA FORMEN PART DE LA NOSTRA COMUNITAT</b></h3>
              </div>
              
             <intrinsicdesign/> 
            </section>
      </div>
    </section>
      
  </template>

  <style>
  
    .btn-custom-violeta {
        background-color: #222b73; /* Color violeta oscuro personalizado */
        color: #ffffff; /* Color del texto, puedes ajustarlo según tus preferencias */
        /* Otros estilos personalizados si es necesario */
    }


  .motiv{
    margin-bottom:5rem ;

  }
  .data p{
    text-align: center;
    margin-top: 2rem;

  }
  .container .text {
    text-align: center;
  }
  
  .titol{
  color: white;
  margin-top: 2rem ;
  margin-bottom: 2rem;
  
  
 
  justify-content: center;
  align-content: end;
  
  display: grid;
  position: relative;
  width: 100%;
  height: auto;
  padding:2em;
  background: #000000;
  }
  
.table{
    margin-bottom: 5rem;
    text-align: center;
}
.form{
    margin-left:20% ;
    margin-right: 20% ;
}



</style>
  
  
  
  
      <script>
        import intrinsicdesign from '@/components/intrinsicdesign.vue';
      export default {
  components: { intrinsicdesign },
          data() {
          return {
              // Input nombre
              nombre: '',
              // Input edad
              edad: '',
              // Ver o no ver el formulario de actualizar
              formActualizar: false,
              // La posición de tu lista donde te gustaría actualizar 
              idActualizar: -1,
              // Input nombre dentro del formulario de actualizar
              nombreActualizar: '',
              // Input edad dentro del formulario de actualizar
              edadActualizar: '',
              // Lista de personas
              personas: [] 
           }
      },
          methods: {
              crearPersona: function () {
                  
                  // Anyadimos a nuestra lista
                  this.personas.push({
                      id: + new Date(),
                      nombre: this.nombre,
                      edad: this.edad
                  });
                  
                  // Vaciamos el formulario de añadir
                  this.nombre = '';
                  this.edad = '';
              },
              verFormActualizar: function (persona_id) {
                  
                  // Antes de mostrar el formulario de actualizar, rellenamos sus campos
                  this.idActualizar = persona_id;
                  this.nombreActualizar = this.personas[persona_id].nombre;
                  this.edadActualizar = this.personas[persona_id].edad;
                  
                  // Mostramos el formulario
                  this.formActualizar = true;
              },
              borrarPersona: function (persona_id) {
                  
                  // Borramos de la lista
                  this.personas.splice(persona_id, 1);
              },
              guardarActualizacion: function (persona_id) {
                  // Ocultamos nuestro formulario de actualizar
                  this.formActualizar = false;
  
                  // Actualizamos los datos
                  this.personas[persona_id].nombre = this.nombreActualizar;
                  this.personas[persona_id].edad = this.edadActualizar;
              }
          }
  
      }
  </script>